table,
td,
th {
  border: 1px solid black;
  border-collapse: collapse;
}
th,
tr,
td {
  padding: 0.5rem 1.5rem 0.5rem 1.5rem;
}

.tableContainer {
  margin-top: 5rem;
  overflow-x: auto;
}
td > button {
  margin-left: 0.3rem;
  border: 1px solid black;
  border-radius: 4px;
}
td > button:first-child {
  color: whitesmoke;
  background-color: #dd2529;
}
td > button:nth-child(2) {
  background-color: rgb(38, 182, 28);
}
th {
  background-color: #04aa6d;
  color: #fff;
}
