.App {
  font-family: sans-serif;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  row-gap: 1rem;
}
.mainCont {
  display: flex;
  flex-direction: row;
  column-gap: 1rem;
}
.labelContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 0.5rem;
}
.inputContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  row-gap: 0.5rem;
}
